/*
Colour
===============================================*/
.progress__money {
  margin-top: -32px; }

progress {
  height: 32px;
  width: 100%;
  margin: 0 0 32px;
  background: #FFFFFF;
  border: 1px solid #4F820D; }

progress::-moz-progress-bar {
  background: #4F820D; }

progress::-webkit-progress-bar {
  background: transparent; }

progress::-webkit-progress-value {
  background: #4F820D; }



